.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  opacity: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.no-scroll {
  height: 100vh;
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 320px;
  font-family: "Poppins", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

.container {
  max-width: 1366px;
  margin: 0 auto;
  padding-left: 60px;
  padding-right: 60px; }

@media (max-width: 1023px) {
  .container {
    padding-left: 45px;
    padding-right: 45px; } }

@media (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px; } }

.search {
  display: flex;
  align-items: center; }

.search label {
  display: flex;
  align-items: center;
  font-size: 0; }

.search svg {
  width: 14px;
  height: 14px; }

.search input {
  width: 252px;
  padding-top: 5px;
  padding-bottom: 1px;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
  line-height: 21px;
  border: none; }
  .search input::placeholder {
    color: #9b9997; }
  .search input:focus {
    outline: none; }
  .search input:focus::placeholder {
    color: #1d1613; }

.search__btn {
  display: none; }

@media (max-width: 1023px) {
  .search {
    width: 100%;
    background-color: rgba(255, 255, 255, 0.15);
    border-radius: 21px; }
    .search:focus-within {
      background-color: rgba(255, 255, 255, 0.3); }
  .search label {
    padding-left: 12px;
    padding-top: 8px;
    padding-bottom: 8px; }
  .search svg {
    width: 16px;
    height: 16px;
    fill: #ffffff; }
  .search input {
    width: 100%;
    padding-top: 11px;
    padding-bottom: 8px;
    color: #ffffff;
    background-color: transparent; }
    .search input::placeholder {
      font-size: 0; } }

.user-block {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap; }

.user-block__btn {
  margin-bottom: 5px;
  margin-right: 35px;
  font-size: 14px;
  line-height: 21px;
  color: #1d1613; }
  .user-block__btn:last-child {
    margin-right: 0; }
  .user-block__btn:hover, .user-block__btn:focus {
    color: rgba(29, 22, 19, 0.5); }
  .user-block__btn:focus {
    outline: none; }
  .user-block__btn:active {
    color: rgba(29, 22, 19, 0.3); }

.user-block svg {
  display: none; }

.user-block__cart-counter {
  margin-left: 3px; }

@media (max-width: 1023px) {
  .user-block {
    justify-content: flex-end;
    width: 256px; }
  .user-block__btn {
    font-size: 18px;
    line-height: 23px; }
    .user-block__btn--cart {
      font-size: 0;
      line-height: 0; }
  .user-block svg {
    display: inline;
    margin-right: -4px;
    vertical-align: middle;
    stroke: #1d1613;
    fill: transparent; }
  .user-block--no-js {
    width: auto; }
  .user-block--no-js .user-block__btn {
    margin-left: 35px;
    margin-right: 0;
    font-size: 14px;
    line-height: 21px;
    color: white; }
    .user-block--no-js .user-block__btn--cart {
      font-size: 0; }
    .user-block--no-js .user-block__btn:first-child {
      margin-left: 0; }
  .user-block--no-js .user-block__cart-icon,
  .user-block--menu-opened .user-block__cart-icon {
    stroke: #ffffff; } }

@media (max-width: 767px) {
  .user-block {
    width: 75px; }
  .user-block--no-js .user-block__btn {
    margin-left: 15px; } }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }
  .main-nav__list--secondary {
    display: none; }

.main-nav__item {
  margin-right: 35px; }
  .main-nav__item:last-child {
    margin-right: 0; }

.main-nav__link {
  display: block;
  line-height: 24px;
  color: #1d1613; }
  .main-nav__link:hover, .main-nav__link:focus {
    color: rgba(29, 22, 19, 0.5); }
  .main-nav__link:focus {
    outline: none; }
  .main-nav__link:active {
    color: rgba(29, 22, 19, 0.3); }

@media (max-width: 1023px) {
  .main-nav__list {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 40px; }
    .main-nav__list--secondary {
      display: flex; }
    .main-nav__list:last-child {
      margin-bottom: 0; }
  .main-nav__item {
    margin-bottom: 21px; }
    .main-nav__item:last-child {
      margin-bottom: 0; }
  .main-nav__link {
    font-size: 24px;
    line-height: 36px;
    color: #ffffff; }
  .main-nav__list--secondary .main-nav__link {
    font-size: 18px;
    line-height: 23px;
    color: rgba(255, 255, 255, 0.75); } }

@media (max-width: 767px) {
  .main-nav__list {
    margin-bottom: 30px; } }

.page-header__top {
  border-bottom: 1px solid #f6f5f4; }

.page-header__top-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 41px;
  padding-bottom: 18px;
  padding-left: 98px;
  padding-right: 98px; }

.page-header__top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  order: 2;
  width: calc(50% + 33px); }

.page-header__menu-container {
  display: flex;
  justify-content: center;
  padding-top: 21px;
  padding-bottom: 16px;
  padding-left: 98px;
  padding-right: 98px; }

.page-header__search {
  order: 1;
  margin-top: 8px; }

.page-header__logo-link {
  line-height: 0; }

.page-header__logo {
  width: 66px;
  height: 66px;
  fill: #a87b62; }

.page-header__user-block {
  order: 1;
  margin-top: 12px;
  margin-left: 50px; }

.page-header__toggle {
  display: none; }

.page-header__nav {
  margin-right: -10px; }

@media (max-width: 1023px) {
  .page-header {
    position: relative; }
  .page-header__top {
    border: none; }
  .page-header__top-container {
    flex-direction: column;
    align-items: flex-end;
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 45px;
    padding-right: 45px; }
  .page-header__top-wrapper {
    align-items: center;
    order: 0;
    width: 100%; }
  .page-header__menu {
    position: absolute;
    top: 100%;
    left: 0;
    display: none;
    width: 100%; }
  .page-header__menu-container {
    flex-direction: column;
    padding-top: 2px;
    padding-left: 45px;
    padding-right: 45px;
    background-color: #a87b62; }
  .page-header__logo-link {
    order: 2; }
  .page-header__toggle {
    position: relative;
    order: 1;
    display: block;
    width: 25px;
    height: 25px;
    margin-right: 281px;
    padding: 0;
    font-size: 0;
    background-color: transparent;
    border: none; }
    .page-header__toggle::before {
      position: absolute;
      content: "";
      top: 50%;
      left: 50%;
      width: 25px;
      height: 2px;
      background-color: #000000;
      box-shadow: 0 8px 0 0 #000000, 0 -8px 0 0 #000000;
      transform: translate(-50%, -50%); }
  .page-header__user-block {
    order: 3;
    margin-top: 5px; }
  .page-header__search {
    order: 0;
    margin-bottom: 48px; }
  .page-header--menu-opened,
  .page-header--no-js {
    background-color: #a87b62; }
  .page-header--menu-opened .page-header__menu {
    display: block; }
  .page-header--menu-opened .page-header__menu-container {
    padding-bottom: 67px; }
  .page-header--menu-opened .page-header__toggle::before, .page-header--menu-opened .page-header__toggle::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    width: 35px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: none; }
  .page-header--menu-opened .page-header__toggle::before {
    transform: translate(-50%, -50%) rotate(45deg); }
  .page-header--menu-opened .page-header__toggle::after {
    transform: translate(-50%, -50%) rotate(-45deg); }
  .page-header--menu-opened .page-header__nav {
    padding-bottom: 44px; }
  .page-header--menu-opened .page-header__login-btn {
    color: #ffffff; }
  .page-header--no-js .page-header__top-container {
    justify-content: flex-end;
    padding-bottom: 24px; }
  .page-header--no-js .page-header__top-wrapper {
    width: calc(50% + 33px);
    margin-bottom: 32px; }
  .page-header--no-js .page-header__menu {
    position: static;
    display: block; }
  .page-header--no-js .page-header__menu-container {
    padding-top: 24px;
    padding-bottom: 80px; }
  .page-header--menu-opened .page-header__logo,
  .page-header--no-js .page-header__logo {
    fill: #ffffff; }
  .page-header--no-js .page-header__toggle {
    display: none; }
  .page-header--no-js .page-header__search {
    margin-bottom: 0; } }

@media (max-width: 767px) {
  .page-header__top-container {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 15px;
    padding-right: 15px; }
  .page-header__menu-container {
    padding-left: 15px;
    padding-right: 15px; }
  .page-header__logo {
    width: 52px;
    height: 52px; }
  .page-header__toggle {
