  .page-header__search {
    margin-bottom: 38px; }
  .page-header--menu-opened .page-header__nav {
    padding-bottom: 34px; }
  .page-header--menu-opened .page-header__menu-container,
  .page-header--no-js .page-header__menu-container {
    padding-bottom: 37px; }
  .page-header--no-js .page-header__top-container {
    padding-bottom: 19px; }
  .page-header--no-js .page-header__top-wrapper {
    width: calc(50% + 26px);
    margin-bottom: 22px; }
  .page-header--no-js .page-header__menu-container {
    padding-top: 19px; } }
